<template>
  <div class="flex">
    <aside>
        <div class="main" ></div>
        <Sidebar :list="list" base="systemset" title="系统管理"/>
    </aside>
    <section>
        <div class="bg">
            <router-view :route="route"/>
        </div>
    </section>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Sidebar from '@/components/common/Sidebar';
export default {
  data () {
    return {
        list: [
            {
                txt: '用户管理',
                url: 'usermanage'
            },
            {
                txt: '角色管理',
                url: 'rolemanage'
            },
            {
                txt: '人员审核',
                url: 'peopleaudit'
            },
            {
                txt: '知识库上传',
                url: 'knowledgeup'
            }
        ]
    }
  },

  components: {
      Sidebar
  },

  computed: {
    ...mapGetters({
      route: 'route'
    })
  },

  mounted: function() {
  },

  methods: {
      
  }
}
</script>

<style lang='scss' scoped>
.flex {
    height: 100%;
}
aside {
    width: 260px;
    font-size: 14px;
    color: #999999;
}
section {
    width: calc(100% - 260px);
    padding: 20px;
    .bg {
        width: 100%;
        height: 100%;
        background: rgba(0,0,0,0.8);
        border-radius: 10px;
    }
}
</style>



